"use client"

import { useState } from "react"
import { useQuery } from "@tanstack/react-query"
import { ArrowLeft, Plus, Minus, IndianRupee, TrendingUp, TrendingDown } from "lucide-react"
import { useRouter } from "next/navigation"
import { Card, Button, Badge, SectionLoader } from "@/components/ui"
import { formatPaise } from "@/lib/constants"
import { formatDateTime } from "@/lib/utils"
import toast from "react-hot-toast"

declare global {
  interface Window {
    Razorpay: any
  }
}

const AMOUNT_OPTIONS = [50000, 100000, 200000, 500000] // in paise

export default function WalletPage() {
  const router = useRouter()
  const [selectedAmount, setSelectedAmount] = useState(100000)
  const [loading, setLoading] = useState(false)

  const { data, isLoading, refetch } = useQuery({
    queryKey: ["customer-profile"],
    queryFn: async () => {
      const res = await fetch("/api/customer/profile")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  const handlePayment = async () => {
    setLoading(true)

    try {
      // Create order
      const orderRes = await fetch("/api/payment/create-order", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          amountPaise: selectedAmount,
          purpose: "wallet_topup",
        }),
      })

      const orderData = await orderRes.json()
      if (!orderRes.ok) throw new Error(orderData.error)

      // Load Razorpay script if not loaded
      if (!window.Razorpay) {
        await loadRazorpayScript()
      }

      // Open Razorpay checkout
      const options = {
        key: orderData.data.keyId,
        amount: orderData.data.amount,
        currency: orderData.data.currency,
        name: "Maa Ilay",
        description: orderData.data.description,
        order_id: orderData.data.orderId,
        prefill: {
          name: orderData.data.customerName,
          email: orderData.data.customerEmail,
          contact: orderData.data.customerPhone,
        },
        theme: {
          color: "#16a34a",
        },
        handler: async (response: any) => {
          // Verify payment
          const verifyRes = await fetch("/api/payment/verify", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(response),
          })

          const verifyData = await verifyRes.json()
          if (verifyRes.ok) {
            toast.success("Payment successful!")
            refetch()
          } else {
            toast.error(verifyData.error || "Payment verification failed")
          }
        },
        modal: {
          ondismiss: () => {
            setLoading(false)
          },
        },
      }

      const razorpay = new window.Razorpay(options)
      razorpay.open()

    } catch (err: any) {
      toast.error(err.message || "Payment failed")
    } finally {
      setLoading(false)
    }
  }

  if (isLoading) return <SectionLoader />

  const wallet = data?.data?.customer?.wallet
  const transactions = wallet?.transactions || []

  return (
    <div className="p-4">
      {/* Header */}
      <div className="flex items-center gap-4 mb-6">
        <button
          onClick={() => router.back()}
          className="h-10 w-10 flex items-center justify-center rounded-xl bg-neutral-100"
        >
          <ArrowLeft className="h-5 w-5" />
        </button>
        <h1 className="text-xl font-bold text-neutral-900">Wallet</h1>
      </div>

      {/* Balance Card */}
      <Card className="bg-gradient-to-br from-primary-600 to-primary-700 text-white mb-6">
        <p className="text-primary-100 text-sm mb-1">Available Balance</p>
        <p className={`text-3xl font-bold ${(wallet?.balancePaise || 0) < 0 ? 'text-red-200' : ''}`}>
          {formatPaise(wallet?.balancePaise || 0)}
        </p>
        {(wallet?.balancePaise || 0) < 0 && (
          <p className="text-red-200 text-sm mt-2">
            Please add money to continue deliveries
          </p>
        )}
      </Card>

      {/* Add Money */}
      <Card className="mb-6">
        <h2 className="font-semibold text-neutral-900 mb-4">Add Money</h2>
        
        <div className="grid grid-cols-2 gap-3 mb-4">
          {AMOUNT_OPTIONS.map((amount) => (
            <button
              key={amount}
              onClick={() => setSelectedAmount(amount)}
              className={`p-3 rounded-xl border-2 transition-all ${
                selectedAmount === amount
                  ? "border-primary-600 bg-primary-50"
                  : "border-neutral-200 hover:border-neutral-300"
              }`}
            >
              <span className={`font-semibold ${
                selectedAmount === amount ? "text-primary-600" : "text-neutral-900"
              }`}>
                {formatPaise(amount)}
              </span>
            </button>
          ))}
        </div>

        <Button
          onClick={handlePayment}
          loading={loading}
          fullWidth
          size="lg"
        >
          <Plus className="h-5 w-5" />
          Add {formatPaise(selectedAmount)}
        </Button>
      </Card>

      {/* Transaction History */}
      <div>
        <h2 className="font-semibold text-neutral-900 mb-4">Recent Transactions</h2>
        
        {transactions.length === 0 ? (
          <Card className="text-center py-8">
            <IndianRupee className="h-8 w-8 text-neutral-300 mx-auto mb-2" />
            <p className="text-neutral-500">No transactions yet</p>
          </Card>
        ) : (
          <div className="space-y-2">
            {transactions.map((tx: any) => (
              <Card key={tx.id} padding="sm">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-3">
                    <div className={`h-10 w-10 rounded-xl flex items-center justify-center ${
                      tx.amountPaise > 0 ? "bg-green-100" : "bg-red-100"
                    }`}>
                      {tx.amountPaise > 0 ? (
                        <TrendingUp className="h-5 w-5 text-green-600" />
                      ) : (
                        <TrendingDown className="h-5 w-5 text-red-600" />
                      )}
                    </div>
                    <div>
                      <p className="font-medium text-neutral-900 text-sm">
                        {tx.description}
                      </p>
                      <p className="text-xs text-neutral-500">
                        {formatDateTime(tx.createdAt)}
                      </p>
                    </div>
                  </div>
                  <span className={`font-semibold ${
                    tx.amountPaise > 0 ? "text-green-600" : "text-red-600"
                  }`}>
                    {tx.amountPaise > 0 ? "+" : ""}{formatPaise(tx.amountPaise)}
                  </span>
                </div>
              </Card>
            ))}
          </div>
        )}
      </div>
    </div>
  )
}

function loadRazorpayScript(): Promise<void> {
  return new Promise((resolve) => {
    const script = document.createElement("script")
    script.src = "https://checkout.razorpay.com/v1/checkout.js"
    script.onload = () => resolve()
    document.body.appendChild(script)
  })
}
